.btn--main {
  border-radius: 15px;
  color: #fff;
  cursor: pointer;
  padding: 5px;
  border: none;
}

.btn--primary {
  background: rgb(85, 88, 237);
}

.btn--secondary {
  background: rgb(80, 82, 84);
}

.btn--small {
  font-size: 14px;
}

.btn--medium {
  font-size: 18px;
}
